import React, { useEffect, useState, useContext } from "react";
import "assets/scss/mimir/question.scss";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

// @material-ui/icons

// core components
import Footer from "components/Footer/Footer.js";

import styles from "assets/jss/material-kit-react/views/questionPage.js";
import { useHistory } from "react-router-dom";
import GridContainer from "components/Grid/GridContainer";
import GridItem from "components/Grid/GridItem";
import "froala-editor/js/plugins.pkgd.min.js";
import "froala-editor/css/froala_style.min.css";
import "froala-editor/css/froala_editor.pkgd.min.css";
import "assets/css/editor.css";

import FroalaEditorComponent from "react-froala-wysiwyg";
import {
  InputBase,
  Select,
  MenuItem,
  withStyles,
  Button,
  Typography
} from "@material-ui/core";
import { Store } from "store";
import { changeQuestionData } from "actions/question";
import { QUESTION_EDITOR_CONFIGS } from "constants/question";
import BootstrapInput from "components/BootstrapInput/BootstrapInput";
import { validateQuestionForm } from "validation/question";
import { updateQuestion, addQuestion } from "services/question";
import Tags from "./components/Tags";

const useStyles = makeStyles(styles);

export default function QuestionsPage(props) {
  const { state, dispatch } = useContext(Store);
  const { subjects, allCategories } = state.subjects;
  const { questionData } = state.question;
  const { body, categoryId, subjectId, tags, title } = questionData;
  const [error, setError] = useState("");
  const [submitLoader, setSubmitLoader] = useState(false);
  const classes = useStyles();
  const handleQuestionData = (key, value) => {
    delete error[key];
    setError(error);
    changeQuestionData(questionData, key, value, dispatch);
  };
  const getCategoryBySubject = subjectId => {
    if (!Array.isArray(allCategories) || allCategories.length === 0) {
      return [];
    }
    return allCategories.filter(
      sub => sub.subjectId.toString() === subjectId.toString()
    );
  };
  const submitQuestion = async () => {
    const error = validateQuestionForm(questionData);
    if (Object.keys(error).length > 0) {
      setError(error);
      return;
    }
    setSubmitLoader(true);
    try {
      const res = await addQuestion(questionData);
      if (res && res.status) {
        alert(res.message);
      }
    } catch (e) {
      console.error("fetching error to add question", e);
    } finally {
      setSubmitLoader(false);
    }
  };
  return (
    <div className="ask-question-container">
      <GridContainer>
        <GridItem xs={12} sm={12} md={8}>
          <div className={classes.container}>
            <h3 className={classes.title}>Ask a Question</h3>
            <div className="subject-select-container field-container">
              <div>
                <label className={classes.titleLabel}>
                  Subject<sup>*</sup>
                </label>
                <Select
                  labelId="demo-customized-select-label"
                  id="demo-customized-select"
                  className="subject-dropdown"
                  value={subjectId}
                  onChange={event =>
                    handleQuestionData("subjectId", event.target.value)
                  }
                  input={<BootstrapInput />}
                >
                  <MenuItem value="">
                    <em>Select Subject</em>
                  </MenuItem>
                  {Array.isArray(subjects) &&
                    subjects.length > 0 &&
                    subjects.map(sub => (
                      <MenuItem value={sub.id}>{sub.name}</MenuItem>
                    ))}
                </Select>
                {error.subjectId && (
                  <Typography variant="caption" display="block" color="error">
                    {error.subjectId}
                  </Typography>
                )}
              </div>
              <div>
                <label className={classes.titleLabel}>Category</label>
                <Select
                  labelId="demo-customized-select-label"
                  id="demo-customized-select"
                  className="subject-dropdown"
                  value={categoryId}
                  onChange={event =>
                    handleQuestionData("categoryId", event.target.value)
                  }
                  input={<BootstrapInput />}
                >
                  <MenuItem value="">
                    <em>Select Category</em>
                  </MenuItem>
                  {getCategoryBySubject(subjectId).map(cat => (
                    <MenuItem value={cat.id}>{cat.name}</MenuItem>
                  ))}
                </Select>
              </div>
            </div>
            <div className="field-container">
              <label className={classes.titleLabel}>
                Title<sup>*</sup>
              </label>
              <InputBase
                className={classes.titleInput}
                placeholder="Enter Title"
                value={title}
                onChange={event =>
                  handleQuestionData("title", event.target.value)
                }
                inputProps={{
                  "aria-label": "Title"
                }}
              />
              {error.title && (
                <Typography variant="caption" display="block" color="error">
                  {error.title}
                </Typography>
              )}
            </div>
            <div className="field-container">
              <label className={classes.titleLabel}>
                Body<sup>*</sup>
              </label>
              <div id="question-editor">
                <FroalaEditorComponent
                  tag="textarea"
                  config={QUESTION_EDITOR_CONFIGS}
                  model={body}
                  onModelChange={value => handleQuestionData("body", value)}
                />
              </div>
              {error.body && (
                <Typography variant="caption" display="block" color="error">
                  {error.body}
                </Typography>
              )}
            </div>
            <div className="field-container">
              <label className={classes.titleLabel}>Tags (Add up to 5 tags to describe what your question is about)</label>
              <div id="question-editor">
                <Tags />
              </div>
            </div>
            <Button
              color="transparent"
              target="_blank"
              className="question-submit"
              onClick={submitQuestion}
              disabled={submitLoader}
            >
              Submit Question
            </Button>
          </div>
        </GridItem>
      </GridContainer>
      <Footer />
    </div>
  );
}
