import { SET_QUESTION_DATA } from "constants/action_names";
import { SET_ALL_TAGS } from "constants/action_names";

export const questionInit = {
  questionData: {
    body: "",
    categoryId: "",
    subjectId: "",
    tags: [],
    title: ""
  },
  allTags: []
};

export function questionsReducer(state = questionInit, action) {
  const { type, payload } = action;
  switch (type) {
    case SET_QUESTION_DATA:
      return {
        ...state,
        question: {
          ...state.question,
          question: payload
        }
      };
    case SET_ALL_TAGS:
      return {
        ...state,
        question: {
          ...state.question,
          allTags: payload
        }
      };
    default:
      return state;
  }
}
