import {
  SET_SUBJECTS,
  SET_ALL_CATEGORIES,
  SET_ALL_DEFINITIONS,
  SET_OVERALL_CATEGORIES
} from "constants/action_names";
import { getAllSubjects } from "services/subject";
import { getCategoriesBySubjects } from "services/subject";
import { getDefinitionsBySubjects } from "services/subject";
import { getDefinitionsByCategory } from "services/subject";
import { getAllCategories } from "services/subject";

export const setAllSubjects = (payload, dispatch) => {
  dispatch({
    type: SET_SUBJECTS,
    payload
  });
};

export const setAllCategories = (payload, dispatch) => {
  dispatch({
    type: SET_ALL_CATEGORIES,
    payload
  });
};

export const setOverallCategories = (payload, dispatch) => {
  dispatch({
    type: SET_OVERALL_CATEGORIES,
    payload
  });
};

export const setAllDefinition = (payload, dispatch) => {
  dispatch({
    type: SET_ALL_DEFINITIONS,
    payload
  });
};

export const fetchAllSubjects = async dispatch => {
  try {
    const subjects = await getAllSubjects();
    if (Array.isArray(subjects)) {
      setAllSubjects(subjects, dispatch);
    }
  } catch (e) {
    console.error("error to fetching all subjects", e);
  }
};

export const fetchCategoriesBySubject = async (subjectId, dispatch) => {
  try {
    setAllCategories(null, dispatch);
    const categories = await getCategoriesBySubjects(subjectId);
    if (Array.isArray(categories)) {
      setAllCategories(categories, dispatch);
    }
  } catch (e) {
    console.error("error to fetching all categories by subject id", e);
  }
};

export const fetchAllCategories = async dispatch => {
  try {
    setOverallCategories(null, dispatch);
    const categories = await getAllCategories();
    if (Array.isArray(categories)) {
      setOverallCategories(categories, dispatch);
    }
  } catch (e) {
    console.error("error to fetching all categories by subject id", e);
  }
};

export const fetchDefinitionsBySubject = async (subjectId, dispatch) => {
  try {
    setAllDefinition(null, dispatch);
    const def = await getDefinitionsBySubjects(subjectId);
    if (Array.isArray(def)) {
      setAllDefinition(def, dispatch);
    }
  } catch (e) {
    console.error("error to fetching all definitions by subject id", e);
  }
};

export const fetchDefinitionsByCategory = async (categoryId, dispatch) => {
  try {
    setAllDefinition(null, dispatch);
    const def = await getDefinitionsByCategory(categoryId);
    if (Array.isArray(def)) {
      setAllDefinition(def, dispatch);
    }
  } catch (e) {
    console.error("error to fetching all definitions by category id", e);
  }
};
