import { SET_QUESTION_DATA } from "constants/action_names";
import { SET_ALL_TAGS } from "constants/action_names";
import { getAllTags } from "services/question";

export const setQuestionData = (payload, dispatch) => {
  dispatch({
    type: SET_QUESTION_DATA,
    payload
  });
};

export const setAllTags = (payload, dispatch) => {
  dispatch({
    type: SET_ALL_TAGS,
    payload
  });
};

export const changeQuestionData = (questionData, key, value, dispatch) => {
  questionData[key] = value;
  setQuestionData(questionData, dispatch);
};

export const fetchAllTags = async dispatch => {
  try {
    const tags = await getAllTags();
    if (Array.isArray(tags)) {
      setAllTags(tags, dispatch);
    }
  } catch (e) {
    console.error("error to fetching all subjects", e);
  }
};
