import axios from "axios";
import { QUESTION,QUESTION_TAGS } from "constants/auth";
import { getCommonHeaders } from "./common";

export const addQuestion = data => {
  return new Promise((resolve, reject) => {
    const options = {
      method: "post",
      url: QUESTION,
      data,
      headers: getCommonHeaders()
    };
    axios(options)
      .then(response => {
        if (response && response.data && response.data.userGuid)
          resolve({ status: true, message: "Subject Added Successfully" });
      })
      .catch(error => reject(error))
      .finally(fin => resolve(fin));
  });
};

export const updateQuestion = body => {
  return new Promise((resolve, reject) => {
    const options = {
      method: "put",
      url: QUESTION,
      body
    };
    axios(options)
      .then(response => {
        if (response && response.data && Array.isArray(response.data.content))
          resolve(response.data.content);
      })
      .catch(error => reject(error))
      .finally(fin => resolve(fin));
  });
};
export const getQuestionById = questionId => {
  return new Promise((resolve, reject) => {
    const options = {
      method: "get",
      url: `${QUESTION}?questionId=${questionId}`
    };
    axios(options)
      .then(response => {
        if (response && response.data && Array.isArray(response.data.content))
          resolve(response.data.content);
      })
      .catch(error => reject(error))
      .finally(fin => resolve(fin));
  });
};

export const getAllTags = () => {
    return new Promise((resolve, reject) => {
      const options = {
        method: "get",
        url: `${QUESTION_TAGS}?pageSize=1000`
      };
      axios(options)
        .then(response => {
          if (response && response.data && Array.isArray(response.data.content))
            resolve(response.data.content);
        })
        .catch(error => reject(error))
        .finally(fin => resolve(fin));
    });
  };
