/* eslint-disable camelcase */
import React, { useReducer, createContext } from "react";
import { subjectsInit } from "reducer/subjects";
import { subjectsReducer } from "reducer/subjects";
import { questionInit } from "reducer/question";
import { questionsReducer } from "reducer/question";

export const Store = createContext();

const initialState = { subjects: subjectsInit, question: questionInit };

const reduceReducers = (...reducers) => (prevState, value, ...args) => {
  return reducers.reduce(
    (newState, reducer) => reducer(newState, value, ...args),
    prevState
  );
};

const reducers = reduceReducers(subjectsReducer, questionsReducer);

export function StoreProvider({ children, initState = initialState }) {
  const [state, dispatch] = useReducer(reducers, initState);
  const value = { state, dispatch };
  return <Store.Provider value={value}>{children}</Store.Provider>;
}
