import React, { useState, useContext } from "react";
import { Store } from "store";
import { changeQuestionData } from "actions/question";

const Tags = () => {
  const { state, dispatch } = useContext(Store);
  const { questionData, allTags } = state.question;
  const { tags } = questionData;
  const [searchText, setSearchText] = useState("");
  const [filteredTags, setFilteredTags] = useState([]);
  const [tagError, setTagError] = useState(false);
  const onSearchText = e => {
    setSearchText(e.target.value);
    if (Array.isArray(allTags)) {
      setFilteredTags(
        allTags.filter(tag => {
          return (
            !tags.includes(tag.name) && tag.name.indexOf(e.target.value) === 0
          );
        })
      );
    }
  };
  const selectTag = tagName => {
    if (tags.length < 5) {
      tags.push(tagName);
      changeQuestionData(questionData, "tags", tags, dispatch);
      setSearchText("");
    } else {
      setTagError(true);
    }
  };
  const removeTag = tagName => {
    const tempTags = tags.filter(tag => {
      return tag !== tagName;
    });
    changeQuestionData(questionData, "tags", tempTags, dispatch);
  };
  return (
    <div className="question-tag-container">
      <div className="tag-input-container">
        {tags.map(tag => (
          <span onClick={() => removeTag(tag)}>
            {tag}
            <i class="fa fa-times" aria-hidden="true"></i>
          </span>
        ))}
        {tags.length <= 5 && (
          <input
            type="text"
            value={searchText}
            onChange={onSearchText}
            className="tag-search-input"
          />
        )}
      </div>
      {searchText && (
        <div className="tag-suggestion-container">
          {filteredTags.length > 0 &&
            filteredTags.map(tag => (
              <div
                className="tag-suggestion-item"
                onClick={() => selectTag(tag.name)}
              >
                <div className="tag-suggestion-name">{tag.name}</div>
                <div className="tag-suggestion-desc">{tag.description}</div>
              </div>
            ))}
          {filteredTags.length === 0 && (
            <div className="tag-suggestion-not-found">
              Search result not found
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default Tags;
