/*eslint-disable*/
import React, { useState, useEffect } from "react";
import DeleteIcon from "@material-ui/icons/Delete";
import IconButton from "@material-ui/core/IconButton";
// react components for routing our app without refresh
import { Link } from "react-router-dom";

// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import Tooltip from "@material-ui/core/Tooltip";

// @material-ui/icons
import { Apps, CloudDownload } from "@material-ui/icons";

// core components
import CustomDropdown from "components/CustomDropdown/CustomDropdown.js";
import Button from "components/CustomButtons/Button.js";

import classNames from "classnames";

import styles from "../../assets/jss/material-kit-react/components/headerLinksStyle.js";
import { useHistory } from "react-router-dom";
import { getCookie } from "utils/cookie.js";
import { loginUser } from "services/user.js";
import { eraseCookie } from "utils/cookie.js";
const useStyles = makeStyles(styles);

export default function HeaderLinks(props) {
  const classes = useStyles();
  const history = useHistory();
  const [userDetails, setUserDetails] = useState(null);
  const [reRender, setReRender] = useState(0);
  const logout = () => {
    eraseCookie("token");
    eraseCookie("userId");
    eraseCookie("userSource");
    setUserDetails(null);
    setReRender(1);
  };
  useEffect(() => {
    const getUserDetails = async response => {
      const accessToken = getCookie("token");
      const userId = getCookie("userId");
      if (accessToken && userId) {
        try {
          const loginReponse = await loginUser({ accessToken, userID: userId });
          if (loginReponse && loginReponse.userId) {
            setUserDetails(loginReponse);
          } else {
            if (loginReponse.message) {
              alert("Session expired");
            }
          }
          setReRender(0);
        } catch (e) {
          console.log("error", e);
        }
      }
    };
    getUserDetails();
  }, [reRender]);

  return (
    <List className={classes.list}>
      <ListItem className={classes.listItem}>
        <Button
          color="transparent"
          target="_blank"
          className={classNames(classes.navLink, classes.postQnNavLink)}
          onClick={() => history.push("/question")}
        >
          Post Question
        </Button>
      </ListItem>
      <ListItem
        className={classes.listItem}
        onClick={() => history.push("/subjects")}
      >
        <Button color="transparent" target="_blank" className={classes.navLink}>
          Subjects
        </Button>
      </ListItem>
      <ListItem className={classes.listItem}>
        <Button color="transparent" target="_blank" className={classes.navLink}>
          How it works
        </Button>
      </ListItem>
      <ListItem
        className={classes.listItem}
        onClick={() => history.push("/login")}
      >
        {userDetails ? (
          <img
            src={userDetails.picture}
            style={{ borderRadius: 40, width: 40, height: 40 }}
            onClick={logout}
          />
        ) : (
          <Button
            color="transparent"
            target="_blank"
            className={classes.navLink}
          >
            LOGIN
          </Button>
        )}
      </ListItem>
    </List>
  );
}
