import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

// @material-ui/icons

// core components
import Footer from "components/Footer/Footer.js";

import styles from "assets/jss/material-kit-react/views/topicPageSections/listSectionStyles.js";
import { useHistory } from "react-router-dom";
import GridContainer from "components/Grid/GridContainer";
import GridItem from "components/Grid/GridItem";
import { Paper, IconButton, InputBase, Button, Badge } from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";

const useStyles = makeStyles(styles);

export default function ListSection(props) {
  const classes = useStyles();
  const history = useHistory();
  const { heading, list, onClick, type } = props;
  return (
    <div className={classes.container}>
      <h3 className={classes.heading}>{heading}</h3>
      <GridContainer>
        {!list && <div>Loading</div>}
        {Array.isArray(list) &&
          list.map(item => (
            <GridItem
              xs={12}
              sm={12}
              md={6}
              onClick={() =>
                onClick(item.id, type === "topic" ? item.topic : item.name)
              }
              className={classes.listContainer}
            >
              <div className={classes.listWrapper}>
                <Badge
                  badgeContent={
                    type === "topic"
                      ? item.topic[0].toUpperCase()
                      : item.name[0].toUpperCase()
                  }
                  classes={{ badge: classes.listBadge }}
                  className={classes.badgeMargin}
                ></Badge>
                <div>{type === "topic" ? item.topic : item.name}</div>
              </div>
            </GridItem>
          ))}
      </GridContainer>
    </div>
  );
}
