import { SET_SUBJECTS } from "constants/action_names";
import { SET_ALL_CATEGORIES } from "constants/action_names";
import { SET_ALL_DEFINITIONS } from "constants/action_names";
import { SET_OVERALL_CATEGORIES } from "constants/action_names";

export const subjectsInit = {
  subjects: null,
  categories: null,
  definitions: null,
  allCategories: null
};

export function subjectsReducer(state = subjectsInit, action) {
  const { type, payload } = action;
  switch (type) {
    case SET_SUBJECTS:
      return {
        ...state,
        subjects: {
          ...state.subjects,
          subjects: payload
        }
      };
    case SET_ALL_CATEGORIES:
      return {
        ...state,
        subjects: {
          ...state.subjects,
          categories: payload
        }
      };
    case SET_OVERALL_CATEGORIES:
      return {
        ...state,
        subjects: {
          ...state.subjects,
          allCategories: payload
        }
      };
    case SET_ALL_DEFINITIONS:
      return {
        ...state,
        subjects: {
          ...state.subjects,
          definitions: payload
        }
      };
    default:
      return state;
  }
}
