import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// nodejs library to set properties for components
import PropTypes from "prop-types";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

// core components
import styles from "assets/jss/material-kit-react/components/parallaxStyle.js";

const useStyles = makeStyles(styles);

export default function Parallax(props) {
  const { filter, className, children, style, image, small, isSmallBg } = props;
  const classes = useStyles();
  const parallaxClasses = classNames({
    [classes.parallax]: true,
    [classes.filter]: filter,
    [classes.small]: small,
    [className]: className !== undefined
  });
  const mainClasses = isSmallBg
    ? classNames(parallaxClasses, classes.smallHeight)
    : parallaxClasses;
  return (
    <div
      className={mainClasses}
      style={{
        ...style,
        backgroundImage: "url(" + image + ")"
      }}
    >
      <div
        style={{
          backgroundColor: "black",
          opacity: "0.5",
          height: "100%",
          width: "100%",
          position: "absolute"
        }}
      ></div>
      {children}
    </div>
  );
}

Parallax.propTypes = {
  className: PropTypes.string,
  filter: PropTypes.bool,
  children: PropTypes.node,
  style: PropTypes.string,
  image: PropTypes.string,
  small: PropTypes.bool
};
