import axios from "axios";
import { USER_SIGNUP } from "constants/auth";
import { GET_CATEGORIES_BY_SUBJECT, GET_ALL_CATEGORIES } from "constants/auth";
import { GET_SUBJECTS } from "constants/auth";
import { GET_DEFINITIONS_BY_SUBJECT } from "constants/auth";
import { GET_DEFINITIONS_BY_CATEGORY } from "constants/auth";

export const getAllSubjects = () => {
  return new Promise((resolve, reject) => {
    const options = {
      method: "get",
      url: GET_SUBJECTS,
      headers: { Authorization: "test" }
    };
    axios(options)
      .then(response => {
        if (response && response.data && Array.isArray(response.data.content))
          resolve(response.data.content);
      })
      .catch(error => reject(error))
      .finally(fin => resolve(fin));
  });
};

export const getCategoriesBySubjects = subjectId => {
  return new Promise((resolve, reject) => {
    const options = {
      method: "get",
      url: GET_CATEGORIES_BY_SUBJECT + "?subjectId=" + subjectId
    };
    axios(options)
      .then(response => {
        if (response && response.data && Array.isArray(response.data.content)) {
          resolve(response.data.content);
        }
      })
      .catch(error => reject(error))
      .finally(fin => resolve(fin));
  });
};

