import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

// @material-ui/icons

// core components
import Footer from "components/Footer/Footer.js";

import styles from "assets/jss/material-kit-react/views/topicPageSections/videosSectionStyles.js";
import { useHistory } from "react-router-dom";
import GridContainer from "components/Grid/GridContainer";
import GridItem from "components/Grid/GridItem";
import { Paper, IconButton, InputBase, Button, Badge } from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";

const useStyles = makeStyles(styles);

export default function VideosList(props) {
  const classes = useStyles();
  const history = useHistory();
  const { videos, onClick, type } = props;
  return (
    <div className={classes.container}>
      <h4 className={classes.heading}>
        {"Videos related to " +
          (type === "topic"
            ? history.location.pathname.split("/")[3].split("-")[1]
            : history.location.pathname.split("/")[2].split("-")[1])}
      </h4>
      <GridContainer>
        {videos.map(item => (
          <GridItem xs={12} sm={12} md={12}>
            <iframe
              className={classes.iframe}
              src={"https://www.youtube.com/embed/" + item}
              frameBorder="0"
              allowFullScreen
              title="video"
            />
            <h4 className={classes.videoTitle}>Learn English in 30 Minutes</h4>
          </GridItem>
        ))}
      </GridContainer>
    </div>
  );
}
