import axios from "axios";
import { USER_LOGIN } from "constants/auth";
import { USER_SIGNUP } from "constants/auth";
import { getCommonHeaders } from "./common";

export const loginUser = ({ accessToken, userID }) => {
  return new Promise((resolve, reject) => {
    const data = { accessToken, userId: userID };
    const options = {
      method: "post",
      url: USER_LOGIN,
      headers: getCommonHeaders()
    };
    axios(options)
      .then(response => {
        resolve(response.data);
      })
      .catch(error => console.log("error", error))
      .finally(fin => resolve(fin));
  });
};

export const registerUser = data => {
  return new Promise((resolve, reject) => {
    const options = {
      method: "post",
      url: USER_SIGNUP,
      data,
      headers: getCommonHeaders()
    };
    axios(options)
      .then(response => {
        resolve(response.data);
      })
      .catch(error => console.log("error", error))
      .finally(fin => resolve(fin));
  });
};
