export const validateQuestionForm = data => {
  const { body = "", subjectId = "", title = "" } = data;
  const errors = {};
  if (body && subjectId && title) {
  } else {
    if (!title) {
      errors.title = "* Title is a required field";
    }
    if (!subjectId) {
      errors.subjectId = "* Subject is a required field";
    }
    if (!body) {
      errors.body = "* Question is a required field";
    }
  }
  return errors;
};
